import React from 'react'
import './Home.css'
import cookies from 'js-cookie';
import toast from 'react-hot-toast';
import supabase from '../../supabaseClient';
import ScrollAnimation from 'react-animate-on-scroll';

const Home=()=> {
    
    const lng=cookies.get('i18next')||"en"
const handelShowCv = async () => {
    try {
        //      Cv
        const { data: lastCv, error: fetchError } = await supabase
            .from('cv')
            .select('cv') //   
            .order('id', { ascending: false }) //   ID    
            .limit(1) //     
            .single(); //   

        if (fetchError) {
            throw fetchError;
        }

        //      
        if (!lastCv || !lastCv.cv) {
            console.error('    .');
            toast.error('    .')
            return;
        }

        //     
        const { data: fileData, error: downloadError } = await supabase.storage
            .from('profile') //   
            .download(lastCv.cv); //     
        if (downloadError) {
            throw downloadError;
        }
        //    
        const url = URL.createObjectURL(fileData);
        const a = document.createElement('a');
        a.href = url;
        a.download = lastCv.cv.split('/').pop(); //     
        document.body.appendChild(a);
        a.click();
        a.remove();
        URL.revokeObjectURL(url); //  
        toast.success("  ")

    } catch (error) {
        console.error('    :', error.message);
        toast.error(`${error.message}`)
        //       
    }
};
  return (
    <section className='home' id='home'>
        <div className='container'>
            <div  
            className='row left'>
            <p className='first'>{lng=="en"?`Hi, Im shadi Alhamdo `:` ,   `}</p>
            <h2 className=''>
            I'm <span className="gradient-text-color">Full-Stack</span>
             Developer.
             </h2>
             <p className='text'>{lng=="en"?`I am a full-stack web programmer.
                 I work on developing websites from the front end using the Reactjs and Vuejs and Nextjs frameworks, and I ensure integrated design using Tailwindcss and Bootstrap.
                  I develop servers and connect to databases in a safe,
                   smooth and professional manner.`:`
                       ,         (),     BOOTSTRAP Tailwindcss ,
                        NODE JS      EXPRESS JS       `}
            </p>
            <div className="buttons">
                    <button className='primary-btn animation-1' href='' >{lng=='en'?`Contact me`:` `} <i className="fa-brands fa-linkedin"></i></button>
                    
                    <button 
                        className='secondary-btn updating' 
                        onClick={handelShowCv}
                    >
                        {lng === 'en' ? 'Download CV' : '  '}
                    </button>
            </div>
        
            </div  >
            <ScrollAnimation  animateIn='flipInX'
  afterAnimatedIn={function afterAnimatedIn(v) {
  	             
  	            }}
            className='row right'>
                <div className='img'>
                    <img src='../../images/home.svg' alt=''/>
                </div>
            </ScrollAnimation >
        </div>
    </section>
  )
}

export default Home